$border: #C6C6C6;
$gray: #8A999C;
$gray-text: #706F6F;
$gray-bg: #F5F5F5;
$green: #93C01F;
$yellow: #FFC944;
$red: #E6332A;
$blue: #0091D6;
.col-xs-5th,
.col-sm-5th,
.col-md-5th,
.col-lg-5th {
    position: relative;
    min-height: 1px;
    padding-right: 10px;
    padding-left: 10px;
    width: 20%;
    float: left; }
.col-5-th {
	-webkit-box-flex: 0;
	-webkit-flex: 0 0 20%;
	-ms-flex: 0 0 20%;
	flex: 0 0 20%;
	max-width: 20%; }
@media (min-width: 768px) {
	.col-sm-5th {
		-webkit-box-flex: 0;
		-webkit-flex: 0 0 20%;
		-ms-flex: 0 0 20%;
		flex: 0 0 20%;
		max-width: 20%; } }
@media (min-width: 992px) {
	.col-md-5th {
		-webkit-box-flex: 0;
		-webkit-flex: 0 0 20%;
		-ms-flex: 0 0 20%;
		flex: 0 0 20%;
		max-width: 20%; } }
@media (min-width: 1200px) {
	.col-lg-5th {
		-webkit-box-flex: 0;
		-webkit-flex: 0 0 20%;
		-ms-flex: 0 0 20%;
		flex: 0 0 20%;
		max-width: 20%; } }
.base_style {
	position: relative;
	min-width: 480px;
	max-width: 1200px;
	font-family: 'Formular', sans-serif;
	font-size: 16px;
	font-weight: 300;
	line-height: 1.1;
	color: #000;
	margin: 0 auto;
	h2 {
		font-size: 22px;
		font-weight: 500;
		@media(min-width:992px) {
			font-size: 26px; } }
	h3 {
		font-size: 18px;
		font-weight: 500;
		@media(min-width:992px) {
			font-size: 26px; } }
	h4 {
		font-size: 16px;
		font-weight: 700; }
	p {
		color: #000;
		font-size: 12px;
		font-weight: 500;
		line-height: 1.6;
		// text-align: left
		text-align: justify;
		@media(min-width:992px) {
			font-size: 14px; } }
	a {
		color: inherit;
		&:hover {
			text-decoration: none;
			color: inherit; } }
	ul, li {
		margin: 0;
		padding: 0;
		list-style-type: none; }
	.my_dropdown {
		height: 26px;
		border: 1px solid $gray;
		position: relative;
		&::before {
			position: absolute;
			content: "";
			width: 4px;
			height: 6px;
			right: 5px;
			top: 50%;
			margin-top: -3px;
			border-left: 3px solid transparent;
			border-right: 3px solid transparent;
			border-top: 6px solid #8a999c; }
		span {
			line-height: 1.8;
			margin-left: 10px;
			font-weight: 300; } }
	.my_dropdown_menu {
		border-radius: 0;
		min-width: 100%;
		color: $gray;
		font-size: 13px;
		font-weight: 300;
		margin: 0;
		li {
			padding: 3px 10px;
			cursor: pointer;
			&:hover {
				background-color: $gray;
				color: #fff; } } }
	.my_button {
		display: block;
		width: 90px;
		height: 26px;
		border: none;
		border-radius: 5px;
		text-align: center;
		background-color: $green;
		font-size: 12px;
		font-weight: 700;
		line-height: 2;
		cursor: pointer;
		&:focus, &:active, &:hover {
			background-color: darken(#93C01F, 10%); }
		@media(min-width:992px) {
			height: 30px;
			line-height: 2.4; } }
	.btn_green {
		background-color: #42b9c0;
		&:focus, &:active, &:hover {
			background-color: darken(#93C01F, 10%);
			color: #ffffff; } }
	.btn_white {
		color: $green;
		background-color: #fff;
		border: 1px solid $green;
		&:focus, &:active, &:hover {
			background-color: darken(#E7EAEB, 10%);
			color: inherit; } }
	.btn_yellow {
		color: #fff;
		background-color: $yellow;
		&:focus, &:active, &:hover {
			background-color: darken(#FFC944, 10%); } }
	.btn_gray {
		background-color: #F5F5F5;
		&:focus, &:active, &:hover {
			background-color: darken(#F5F5F5, 10%); } } }

.form_input, .my_textarea {
	padding: 0 0 0 7px;
	font-size: 16px;
	font-weight: 500;
	border: none;
	border-radius: 5px;
	color: $gray-text;
	outline: none;
	margin-bottom: 15px;
	&:focus {
		box-shadow: none; }
	@media(min-width:992px) {
		font-size: 12px; } }
.form_input {
	height: 40px;
	width: 100%; }
input::-webkit-input-placeholder {
	font-size: 12px;
	color: $gray-text;
	font-weight: 500; }
input::-moz-placeholder /* Firefox 18- */ {
	font-size: 12px;
	color: $gray-text;
	font-weight: 500; }
input::-moz-placeholder /* Firefox 19+ */ {
	font-size: 12px;
	color: $gray-text;
	font-weight: 500; }
input::-ms-input-placeholder {
	font-size: 12px;
	color: $gray-text;
	font-weight: 500; }
textarea::-webkit-input-placeholder {
	// padding-top: 5px
	font-size: 12px;
	color: $gray-text;
	font-weight: 500; }
textarea:-moz-placeholder  /* Firefox 18- */ {
	// padding-top: 5px
	font-size: 12px;
	color: $gray-text;
	font-weight: 500; }
textarea::-moz-placeholder   /* Firefox 19+ */ {
	// padding-top: 5px
	font-size: 12px;
	color: $gray-text;
	font-weight: 500; }
textarea:-ms-input-placeholder {
	// padding-top: 5px
	font-size: 12px;
	color: $gray-text;
	font-weight: 500; }
