.breadcrumbs {
	text-align: left;
	font-size: 13px;
	font-weight: 300;
	margin-top: 5px; }
.blue_text {
	color: $blue;
	text-decoration: underline;
	text-decoration-style: dotted;
	cursor: pointer; }
.m-0-auto {
	margin: 0 auto; }
.d-n-mob-span {
	display: none;
	@media(min-width:768px) {
		display: inline; } }
.d-n-desc {
	@media(min-width:992px) {
		display: none!important; } }
.d-n-992 {
	display: none!important;
	@media(min-width:992px) {
		display: block!important; } }
.flex {
	display: flex; }
.w-0 {
	max-width: 0px; }
.d-b {
	display: block!important; }
.cursor {
	cursor: pointer; }

